.from-ingredient-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 5%;
}
.from-ingredient-container .from-ingredient-card {
  display: flex;
  justify-content: space-between;
  padding: 5%;
  align-items: center;
  width: 70%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.from-ingredient-container .from-ingredient-card div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.from-ingredient-container .from-ingredient-card img {
  width: 100px;
  border-radius: 200px;
}/*# sourceMappingURL=Ingredients.css.map */